import React, { Component } from "react";
import "jquery/dist/jquery.min.js";
import "datatables.net-dt/js/dataTables.dataTables";
import "datatables.net-dt/css/jquery.dataTables.min.css";
import "datatables.net-buttons-dt";
import "datatables.net-buttons/js/buttons.html5.js";
import "datatables.net-buttons-dt/css/buttons.dataTables.css";
import "datatables.net-select-dt";
import "../../assets/css/dashforge.contacts.css";
import $ from "jquery";
import { Edit3, Eye, Trash2 } from "react-feather";
import { getRestaurants,deleteRestaurant } from "../../controllers/RestaurantController";

const Clt=getRestaurants()
export default class DummyRestaurantTable extends Component {
  constructor(props) {
    super(props);
    this.state = { loading: false, tableData: [] };
  }

  shouldComponentUpdate() {
    return true;
  }

  componentDidMount() {
    $("#clientResponsive tfoot th").each(function () {
      var title = $(this).text();
      $(this).html('<input type="text" placeholder="Search ' + title + '" />');
    });
    Clt.then((data) => data)
      .then(
        (json) => {
          this.setState({ isLoaded: true, tableData: json });
        },
        (err) => {
          this.setState({ isLoaded: true, err });
        }
      )
      .then((json) => {
        $("#clientResponsive").DataTable({
          paging: true,
          dom: "Bfrtip",
          responsive: true,
          buttons: [],
          initComplete: function () {
            // Apply the search
            this.api()
              .columns()
              .every(function () {
                var that = this;
                return $("input", this.footer()).on(
                  "keyup change clear",
                  function () {
                    if (that.search() !== this.value) {
                      that.search(this.value).draw();
                    }
                  }
                );
              });
          },
        });
      })
      .catch((err) => {
        console.log(err);
      });
  }
  render() {
    const { tableData } = this.state;
    return (
      <>
        <table id="clientResponsive" className=" table">
          <thead>
            <tr>
              <th
                style={{
                  overflow: "hidden",
                  textOverflow: "ellipsis",
                  whiteSpace: "nowrap",
                  padding: 4,
                }}
              >
                Restaurant Name
              </th>
              <th
                style={{
                  overflow: "hidden",
                  textOverflow: "ellipsis",
                  whiteSpace: "nowrap",
                  padding: 4,
                }}
              >
                Location
              </th>

              <th
                style={{
                  overflow: "hidden",
                  textOverflow: "ellipsis",
                  whiteSpace: "nowrap",
                  padding: 4,
                }}
              >
                Email
              </th>
              <th
                style={{
                  overflow: "hidden",
                  textOverflow: "ellipsis",
                  whiteSpace: "nowrap",
                  padding: 4,
                }}
              >
                Status
              </th>
              <th
                style={{
                  overflow: "hidden",
                  textOverflow: "ellipsis",
                  whiteSpace: "nowrap",
                  padding: 4,
                }}
              >
                Actions
              </th>
            </tr>
          </thead>
          <tbody>
            {tableData.map((data, key) => {
              return (
                <tr key={key}>
                  <td
                    style={{
                      overflow: "hidden",
                      textOverflow: "ellipsis",
                      whiteSpace: "nowrap",
                      padding: 4,
                    }}
                  >
                    {data.restaurant_name}
                  </td>
                  <td
                    style={{
                      overflow: "hidden",
                      textOverflow: "ellipsis",
                      whiteSpace: "nowrap",
                      padding: 4,
                    }}
                  >
                    {data.address.area+', '+data.address.city+', '+data.address.postal_code}
                  </td>
                  <td
                    style={{
                      overflow: "hidden",
                      textOverflow: "ellipsis",
                      whiteSpace: "nowrap",
                      padding: 4,
                    }}
                  >
                    {data.email}
                  </td>

                  <td
                    style={{
                      overflow: "hidden",
                      textOverflow: "ellipsis",
                      whiteSpace: "nowrap",
                      padding: 4,
                      color: "green",
                    }}
                  >
                    {data.status || "Active"}
                  </td>
                  <td
                    style={{
                      overflow: "hidden",
                      textOverflow: "ellipsis",
                      whiteSpace: "nowrap",
                      padding: 4,
                    }}
                  >
                    <div className="row row-sm ml-2">
                      <div className="px-1">
                        <Eye size={18} color="green" />
                      </div>
                      <div className="px-1">
                        <Edit3 size={18} color="green" />
                      </div>
                      <div className="px-1">
                        <Trash2 size={18} color="red" />
                      </div>
                    </div>
                  </td>
                </tr>
              );
            })}
          </tbody>
          <tfoot>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </tfoot>
        </table>
      </>
    );
  }
}
