import React, { useState } from "react";

function DynamicMeal() {
    const [inputList, setInputList] = useState([{ firstName: "", lastName: "" }]);

    // handle input change
    const handleInputChange = (e, index) => {
        const { name, value } = e.target;
        const list = [...inputList];
        list[index][name] = value;
        setInputList(list);
    };

    // handle click event of the Remove button
    const handleRemoveClick = index => {
        const list = [...inputList];
        list.splice(index, 1);
        setInputList(list);
    };

    // handle click event of the Add button
    const handleAddClick = () => {
        setInputList([...inputList, { firstName: "", lastName: "" }]);
    };

    return (
        <>
            {inputList.map((x, i) => {
                return (
                    <>
                        <div className="row mg-t-10 mx-1">
                            <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                Available as
          </label>
                            <div className="col-6 col-sm-4">
                                <div class="custom-control custom-checkbox">
                                    <input
                                        type="checkbox"
                                        class="custom-control-input"
                                        id="customCheck1"
                                    />
                                    <label class="custom-control-label" for="customCheck1">
                                        Lunch
              </label>
                                </div>
                            </div>
                            <div className="col-6 col-sm-4">
                                <div class="custom-control custom-checkbox">
                                    <input
                                        type="checkbox"
                                        class="custom-control-input"
                                        id="customCheck1"
                                    />
                                    <label class="custom-control-label" for="customCheck1">
                                        Dinner
              </label>
                                </div>
                            </div>
                        </div>
                        <div className="row mg-t-10 mx-1">
                            <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                Meal type
          </label>
                            <div className="col-6 col-sm-4">
                                <div class="custom-control custom-checkbox mx-3">
                                    <input
                                        type="checkbox"
                                        class="custom-control-input"
                                        id="customCheck1"
                                    />
                                    <label class="custom-control-label" for="customCheck1">
                                        Veg
              </label>
                                </div>
                            </div>
                            <div className="col-6 col-sm-4">
                                <div class="custom-control custom-checkbox mx-3">
                                    <input
                                        type="checkbox"
                                        class="custom-control-input"
                                        id="customCheck1"
                                    />
                                    <label class="custom-control-label" for="customCheck1">
                                        Non Veg
              </label>
                                </div>
                            </div>
                        </div>
                        <div className="row row-sm">
                            <div className="col-6 col-sm-4 ">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                    Meal Name
                    </label>
                                <p className="mg-b-0">
                                    <input

                                        name="restaurant_name"


                                    />
                                </p>
                            </div>
                            <div className="col-6 col-sm-4 ">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                    Image
                    </label>
                                <div className="custom-file px-2">
                                    <input
                                        type="file"
                                        className="custom-file-input"
                                        id="customFile"
                                    />
                                    <label className="custom-file-label" for="customFile">
                                        Choose file
                    </label>
                                </div>
                            </div>
                        </div>

                        <div className="row row-sm">
                            <div className="col-10 col-sm-8 mg-sm-t-10">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                    Description
                    </label>
                                <p className="mg-b-0">
                                    <textarea

                                        name="phone"

                                    />
                                </p>
                            </div>
                        </div>

                        <div className="row row-sm">
                            <div className="col-sm-4 mg-t-20 mg-sm-t-30">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                    Add On
                    </label>
                                <p className="mg-b-0">
                                    <input

                                        name="locality"

                                    />
                                </p>
                            </div>
                            <div className="col-sm-4 mg-t-20 mg-sm-t-30">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                    price
                    </label>
                                <p className="mg-b-0">
                                    <input

                                        name="city"

                                    />
                                </p>
                            </div>
                            <div className="col-6 col-sm-4 mg-t-20 mg-sm-t-30">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">

                                </label>
                                <p className="mg-b-10">
                                    <button

                                        name="state"
                                        type="button"
                                        className="btn btn-sm btn-primary">Add More</button>


                                </p>
                            </div>
                            <div className="col-6 col-sm-4 mg-t-20 mg-sm-t-30">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                </label>
                                <p className="mg-b-0">
                                    <button
                                        className="btn btn-sm btn-primary"
                                    > Save and next</button>
                                </p>
                            </div>



                            <div className="col-6 col-sm-4 mg-t-20 mg-sm-t-30">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                </label>
                                <p className="mg-b-0">
                                    <button

                                        className="btn btn-sm btn-danger"
                                    >Cancel</button>
                                </p>
                            </div>
                        </div>


                        <div className="btn-box">
                            {inputList.length !== 1 && <button
                                className="mr10"
                                onClick={() => handleRemoveClick(i)}>Remove</button>}
                            {inputList.length - 1 === i && <button onClick={handleAddClick}>Add</button>}
                        </div>
                    </>
                );
            })}

        </>
    );
}

export default DynamicMeal;